.video {
  width: 320px;
  margin: 0 auto;

  @media (min-width: 768px) {
    width: 768px;
  }

  @media (min-width: 1200px) {
    width: 1200px;
  }
}

.video__title {
  margin-bottom: 28px;
  font-size: 18px;

  @media (min-width: $tablet-width) {
    margin-bottom: 33px;
    font-size: 30px;
  }
}

.video__lead {
  width: 320px;
  padding: 0 25px;
  margin: 0 auto;
  margin-bottom: 33px;

  @media (min-width: $tablet-width) {
    width: 500px;
    margin-bottom: 79px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 82px;
  }
}

.video__container {
  position: relative;
  @media (min-width: $tablet-width) {
    position: relative;
    width: 648px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 800px;
  }
}

.video__player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

  outline: none;
}

.video__poster {
  @media (min-width: $tablet-width) {
    display: block;
    width: 648px;
    height: 500px;
    margin: 0 auto;
  }
  @media (min-width: $desktop-width) {
  width: 800px;
  }
}

.video__controls {
  position: relative;
  width: 280px;
  height: 66px;
  margin: 0 auto;
  background-color: #F2F2F2;

  @media (min-width: $tablet-width) {
    position: absolute;
    left: 0;
    bottom: 0;

    width: 100%;
    height: 93px;
    background-color: rgba(#000000, 0.6);
  }
}

.video__playback {

  position: absolute;
  left: 0;
  bottom: 100%;

  width: 280px;
  height: 45px;
  background-color: rgba(0, 0, 0, 0.6);

  &:hover {
    cursor: pointer;
  }

  @media (min-width: $tablet-width) {
    bottom: 50%;
    left: 93px;
    right: 166px;
    width: auto;
    background: none;
    transform: translateY(50%);
  }
}

.video__playback-line {
  position: absolute;
  top: 22px;
  left: 22px;
  width: 235px;
  height: 3px;
  background-color: rgba(#ffffff, 0.3);

  @media (min-width: $tablet-width) {
    left: 0;
    width: 100%;
    background-color: rgba(#ffffff, 0.2);
  }
}

.video__playback-slider {
  position: absolute;
  top: -3px;
  left: 0px;
  width: 9px;
  height: 9px;
  background-color: #ffffff;
  border-radius: 50%;

  @media (min-width: $tablet-width) {
    left: 0px;
  }
}

.video__control {
  position: absolute;
  top: 50%;

  transform: translateY(-50%);
}

.video__replay {
  left: 25px;

  @media (min-width: $tablet-width) {
    left: 35px;
  }
}

.video__subtitles {
  right: 74px;

  @media (min-width: $tablet-width) {
    right: 90px;
  }
}

.video__fullscreen {
  right: 23px;

  @media (min-width: $tablet-width) {
    right: 40px;
  }
}

.video__icon {
  fill: #5C6870;

  .video__control:hover & {
    fill: #333333;
  }

  .video__control:active & {
    opacity: 0.3;
    fill: #333333;
  }

  @media (min-width: $tablet-width) {
    fill: #ffffff;

    .video__control:hover & {
      opacity: 0.6;
      fill: #ffffff;
    }

    .video__control:active & {
      opacity: 0.3;
      fill: #ffffff;
    }
  }
}


