.title {
  padding: 0 10px;
  margin-bottom: 30px;

  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #000;
  text-transform: uppercase;
  font-weight: bold;

  @media (min-width: $tablet-width) {
    font-size: 21px;
    line-height: 26px;
  }

  &--large {
    margin-bottom: 23px;
    font-size: 21px;

    @media (min-width: $tablet-width) {
      margin-bottom: 36px;
      font-size: 30px;
      line-height: 26px;
    }
  }
}
