.main-header {
  &--photo {
    margin-bottom: 28px;
  }

  @media (max-width: $mobile-width-only) {
    font-size: 18px;
  }

  @media (min-width: $tablet-width) {
    width: 460px;
    margin: 0 auto;
    margin-bottom: 30px;

    &--inner {
      margin-bottom: 33px;
    }

    & br {
      display: none;
    }
  }
}
