.map {
  position: relative;
  height: 350px;
  overflow: hidden;
}

.map__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;

  width: 27px;
  height: 27px;

  transform: translate(-50%, -50%);
}

.map__img {
  position: absolute;
  top: 0;
  left: 50%;

  max-width: none;
  width: 768px;

  transform: translateX(-50%);

  @media (min-width: $tablet-width) {
    width: 1200px;
  }
}
