.advantages__list {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.advantages__lead {
  margin-bottom: 52px;

  @media (min-width: $desktop-width) {
    margin-bottom: 41px;
  }
}

.advantages__item {
  background-color: #eeeeee;

  @media (min-width: $tablet-width) {
    width: 33.33333%;
  }

  &--picture {
    @media (min-width: $tablet-width) {
      display: flex;
      flex-wrap: wrap;

      width: 100%;
      max-width: 1000px;
      margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
      max-width: none;
    }
  }
}

.advantages__text {
  position: relative;
  padding: 0 20px;
  padding-top: 27px;
  padding-bottom: 24px;

  text-align: center;

  @media (min-width: $tablet-width) {
    padding-top: 43px;
    padding-bottom: 42px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 63px;
  }

  .advantages__item--picture & {
    padding: 0;
    padding-top: 39px;
    padding-bottom: 35px;

    color: #ffffff;

    background-color: #81B3D2;

    @media (min-width: $tablet-width) {
      flex-basis: 0;
      flex-grow: 1;
      padding-left: 5px;
      padding-right: 5px;
      padding-top: 42px;

      &--devil {
        padding-left: 18px;
        padding-right: 18px;
      }
    }

    @media (min-width: $desktop-width) {
      padding-top: 49px;

      &--devil {
        padding-top: 52px;
      }
    }
  }

  @media (max-width: $mobile-width-only) {
    .advantages__item--picture:first-child & {
      padding-bottom: 38px;
      padding-top: 72px;

      background-image: url('../img/white-triangle.svg');
      background-repeat: no-repeat;
      background-position: center top;
      background-size: auto 37px;
    }
  }

  .advantages__item:last-child & {
    padding-bottom: 23px;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 20px;
    right: 20px;

    height: 1px;
    background-color: #D6D6D6;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  .advantages__item:last-child &::after, .advantages__item--picture &::after {
    display: none;
  }
}

.advantages__item-title {
  margin-bottom: 12px;


  & br {
    display: none;

    @media (min-width: $tablet-width) {
      display: inline;
    }
  }

  .advantages__item--picture & {
    color: #ffffff;
  }
}

.advantages__item-number {
  display: block;

  margin-bottom: 16px;

  @media (min-width: $tablet-width) {
    margin-bottom: 23px;
  }
}

.advantages__item-description {
  padding: 0 12px;

  text-transform: uppercase;

  & br {
    display: none;
  }

  @media (min-width: $desktop-width) {
    padding: 0 50px;

    .advantages__item--picture & br {
      display: inline;
    }
  }
}

.advantages__item-figure {
  text-align: center;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    &--devil {
      order: -1;
    }
  }

  @media (min-width: $desktop-width) {
    height: 256px;
  }
}

.advantages__item-image {
  @media (min-width: $desktop-width) {
    position: relative;
    top: -68px;

    .advantages__item-figure--devil & {
      top: -15px;
    }
  }
}


