.page__main {
  max-width: 1200px;
  padding-top: 37px;
  margin: 0 auto;

  &--index {
    padding-top: 33px;
  }

  &--photo {
    padding-top: 25px;
  }

  @media (min-width: $tablet-width) {
    padding-top: 65px;

    &--index {
      padding-top: 46px;
    }

    &--photo {
      padding-top: 59px;
    }
  }

  @media (min-width: $desktop-width) {
    padding-top: 114px;
    &--index {
      padding-top: 57px;
    }

    &--photo {
      padding-top: 101px;
    }
  }
}

.page__footer {
  padding-top: 40px;
  padding-bottom: 23px;

  &--inner {
    padding-top: 78px;
  }

  @media (min-width: $tablet-width) {
    padding: 36px 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &--inner {
      padding-top: 97px;
    }

    &--photo {
      padding-top: 76px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    padding-left: 120px;
    padding-right: 120px;
    margin: 0 auto;

    &--photo {
      padding-top: 120px;
    }
  }
}
