.btn {
  display: block;
  padding: 0 32px;
  padding-top: 16px;
  padding-bottom: 16px;
  margin: 0 auto;

  font-size: 21px;
  line-height: 26px;
  text-align: center;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;

  background-color: #81B3D2;

  &:hover {
    background-color: #5496BD;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);

    background-color: #5496BD;
  }

  &[disabled] {
    background-color: #E5E5E5;
  }
}
