.feedback {
  display: flex;
  flex-wrap: wrap;
}

.feedback__section {
  width: 100%;
  padding: 0 20px;
  padding-top: 21px;
  padding-bottom: 25px;
  border-top: 1px solid #E5E5E5;

  &--attractions {
    padding-top: 17px;
  }

  &--textarea {
    margin-bottom: 26px;
  }

  @media (min-width: $tablet-width) {
    padding-left: 60px;
    padding-right: 60px;
    border: none;

    &--impression {
      display: flex;
      flex-wrap: wrap;
      order: -1;

      width: 50%;
      padding-top: 0;
      padding-bottom: 0;
      padding-right: 28px;
      margin-bottom: 53px;
      border-right: 1px solid #DBDBDB;
    }

    &--fullname {
      width: 50%;
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 53px;
    }

    &--textarea {
      margin-bottom: 32px;
    }
  }

  @media (min-width: $tablet-width) {
    &--attractions {
      padding-top: 27px;
      padding-bottom: 18px;
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 120px;
    padding-right: 120px;

    &--impression {
      padding-right: 127px;
    }

    &--fullname {
      padding-left: 81px;
    }

    &--contacts {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding-bottom: 29px;
    }
  }
}

.feedback__title {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  margin-bottom: 18px;

  &--contacts {
    margin-bottom: 16px;
  }

  &--impression {
    margin-bottom: 20px;
  }

  &--textarea {
    margin-bottom: 14px;
  }

  @media (min-width: $tablet-width) {
    &::after, &::before {
      content: '';
      position: absolute;
      z-index: -1;
    }

    &::before {
      top: 50%;
      left: 0;
      height: 1px;
      width: 100%;
      background-color: #E5E5E5;
    }

    &::after {
      top: 0;
      left: 50%;
      height: 100%;
      width: 402px;
      background-color: #ffffff;
      transform: translateX(-201px);
    }

    &--half-width {
      align-items: flex-start;
      padding: 0;
      margin-bottom: 53px;
      text-align: left;

      &::before, &::after {
        display: none;
      }
    }

    &--contacts {
      margin-bottom: 18px;
    }

    &--textarea {
      margin-bottom: 54px;
    }

    @media (min-width: $desktop-width) {
      &--contacts {
        margin-bottom: 68px;
      }
    }
  }

  @media (min-width: $desktop-width) {
    &--impression {
      text-align: right;
    }
  }
}

.feedback__input-group {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  &--contacts {
    position: relative;
    margin-bottom: 14px;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;

      width: 58px;
      height: 58px;

      background-color: #E6E6E6;
      background-repeat: no-repeat;
    }
  }
  &--phone::after {
    background-image: url('../img/icon-phone.svg');
    background-position: center center;
    background-size: 18px 18px;
  }

  &--email::after {
    background-image: url('../img/icon-mail.svg');
    background-position: center center;
    background-size: 16px 16px;
  }

  @media (min-width: $tablet-width) {
    &--contacts {
      margin-bottom: 25px;
    }
  }

  @media (min-width: $desktop-width) {
    &--contacts {
      flex-wrap: wrap;
      margin-bottom: 0;
    }
  }
}

.feedback__label {
  line-height: 26px;
  text-transform: uppercase;

  margin-right: 14px;

  &--contacts {
    margin-left: 20px;
    margin-bottom: 8px;
  }

  @media (min-width: $desktop-width) {
    &--contacts {
      margin-left: 0;

      & span {
        display: none;
      }
    }
  }
}

.feedback__input {
  width: 0;
  height: 58px;
  flex-grow: 1;

  padding: 14px 19px;
  padding-bottom: 10px;

  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;

  background-color: #F2F2F2;
  border: 4px solid #F2F2F2;
  outline: none;

  &:hover {
    background-color: #E5E5E5;
  }

  &:active, &:focus {
    background-color: #ffffff;
    border: 4px solid #E5E5E5;
  }

  &--contacts {
    width: 100%;
  }

  &::placeholder {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    color: #000000;
  }

  @media (min-width: $desktop-width) {
    &--contacts {
    width: 315px;
    }
  }
}

.feedback__required-note {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline;
    font-size: 14px;
    line-height: 26px;
    font-weight: normal;
    text-transform: uppercase;
  }
}

.feedback__box-list {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;

    &--attractions {
      justify-content: space-between;
      width: 460px;
      padding-top: 22px;
      margin: 0 auto;
    }
  }

  @media (min-width: $desktop-width) {
    &--attractions {
      width: 100%;
    }
  }
}

.feedback__box-item {
  margin-bottom: 14px;

  &:last-child {
    margin-bottom: 0;
  }

  &--checkbox {
    margin-bottom: 24px;
  }

  @media (min-width: $tablet-width) {
    width: 100%;

    &--checkbox {
      width: 44%;
      margin-bottom: 33px;
    }
  }

  @media (min-width: $desktop-width) {
    &--checkbox {
      width: auto;
    }
  }
}

.feedback__box-label {
  display: flex;
  align-items: center;
}

.feedback__box-indicator {
  position: relative;
  width: 56px;
  height: 56px;
  margin-right: 21px;
  background-color: #F2F2F2;
  border-radius: 5px;

  &--radio {
    border-radius: 50%;
  }

  .feedback__radio:checked + &--radio::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 21px;
    height: 21px;
    background-color: #333333;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }

  .feedback__checkbox:checked + &--checkbox {
    background-image: url('../img/icon-tick.svg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 23px 16px;
  }

}

.feedback__box-text {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
}

.feedback__textarea {
  width: 100%;
  height: 160px;
  padding: 5px 12px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;

  background-color: #F2F2F2;
  border: 4px solid #F2F2F2;
  resize: none;
  outline: none;

  &:hover {
    background-color: #E5E5E5
  }

  &::placeholder {
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    color: #000000;
  }

  &:active, &:focus {
    background-color: #ffffff;
    border: 4px solid #E5E5E5;
  }

  @media (min-width: $tablet-width) {
    padding: 12px 16px;
  }
}

.feedback__footer {
  width: 100%;
  @media (min-width: $tablet-width) {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 60px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 120px;
  }
}

.feedback__btn {
  width: 280px;
  padding-bottom: 14px;
  padding-top: 18px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 324px;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    width: 480px;
  }
}
