.social {
  display: flex;
  justify-content: center;

  padding: 0 20px;
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-left: -30px;
  }
}

.social__item {
  margin-right: 6px;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 46px;
  height: 48px;
  background-color: #81B3D2;

  &:hover {
    background-color: #669EC0;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);

    background-color: #5496BD;
  }
}

.social__icon {
  fill: #ffffff;
}
