.copyright {
  text-align: center;

  @media (min-width: $tablet-width) {
  }
}

.copyright__text {
  margin-right: 8px;
  line-height: 26px;
  text-transform: uppercase;
  vertical-align: middle;
}

.copyright__img {
  vertical-align: middle;

  &:hover {
    fill: #81B3D2;
  }

  &:active {
    fill: rgba(0, 0, 0, 0.3)
  }
}
