html {
  background-color: #E7E5E5;
}

body {
  min-width: $mobile-width;
  max-width: 1200px;
  margin: 0 auto;

  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: $font-color;
  font-weight: normal;
  font-style: normal;

  background-color: $bg-color;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2)
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}
