.features {
  display: block;
  width: 320px;
  margin: 0 auto;
  padding: 36px 40px;
  padding-bottom: 34px;
  padding-left: 70px;

  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    width: 100%;
    padding: 0;
    padding-top: 158px;
    padding-bottom: 33px;
  }
}

.features__list {
  @media (min-width: $tablet-width) {
    display: flex;
  }
}

.features__item {
  flex-basis: 0;
  flex-grow: 1;
  position: relative;
  margin-bottom: 26px;

  &:last-child {
    margin-bottom: 0;
  }

  @media(min-width: $tablet-width) {
    &:nth-child(2) {
      order: 1;
    }
  }
}

.features__icon {
  position: absolute;
  height: auto;
  fill: #81B3D2;

  &--accommodation {
    top: -10px;
    left: -49px;
    width: 39px;
    height: 37px;
  }

  &--gift {
    top: -12px;
    left: -45px;
    width: 32px;
    height: 38px;
  }

  &--burger {
    top: -6px;
    left: -52px;
    width: 40px;
    height: 35px;
  }

  @media (min-width: $tablet-width) {
    left: 50%;
    transform: translateX(-50%);
    bottom: 100%;
    top: auto;
    margin-bottom: 26px;

    &--accommodation {
      width: 75px;
      height: 72px;

    }

    &--gift {
      width: 64px;
      height: 75px;

    }

    &--burger {
      width: 76px;
      height: 68px;

    }
  }
}

.features__title {
  padding: 0;
  margin-bottom: 16px;
  text-align: left;

  @media (min-width: $tablet-width) {
    margin-bottom: 24px;

    text-align: center;
  }
}

.features__description {
  text-transform: uppercase;

  & br {
    display: none;
  }

  @media (min-width: $tablet-width) {
    text-align: center;
    padding: 0px;

    & br {
      display: inline;
    }
  }
}
