.hero-header {
  position: relative;

  padding-top: 52px;
  padding-bottom: 66px;

  text-align: center;

  background-image: url('../img/bg-back-mobile@1x.jpg');
  background-repeat: no-repeat;
  background-size: 768px auto;
  background-position: center -228px;

  @media (min-resolution: $retina-dpi) {
    background-image: url('../img/bg-back-mobile@2x.jpg');
  }

  @media (min-width: $tablet-width) {
    padding-top: 95px;
    padding-bottom: 115px;

    background-image: url('../img/bg-back-desktop@1x.jpg');
    background-size: 1200px auto;

    @media (min-resolution: $retina-dpi) {
      background-image: url('../img/bg-back-desktop@2x.jpg');
    }
  }

  @media (min-width: $desktop-width) {
    padding-top: 101px;
    padding-bottom: 120px;
    background-position: center -217px;
  }

  &--index {
    padding-top: 90px;
    padding-bottom: 85px;

    background-position: center top;
    background-size: auto 100%;

    @media (min-width: $tablet-width) {
      padding-top: 77px;
      padding-bottom: 90px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;

    width: 100%;
    height: 57px;

    background-image: url('../img/bg-intro-triangle-desktop.svg');
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 1200px auto;
  }
}

.hero-header__img {
  width: 229px;
  .hero-header--index & {
    width: 282px;
  }

  @media (min-width: $tablet-width) {
    .hero-header & {
      width: 461px;
    }
  }
}

