.section-lead {
  padding: 0 10px;
  margin-bottom: 14px;

  font-size: 14px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;

  &--index {
    margin-bottom: 11px;
    line-height: 26px;
  }

  & br {
    display: none;
  }

  &--photo {
    padding: 0 20px;
    margin-bottom: 38px;
  }

  @media (min-width: $tablet-width) {
    & br {
      display: inline;
    }

    .form-page & {
      margin-bottom: 85px;
    }

    &--photo {
      width: 470px;
      margin: 0 auto;
      margin-bottom: 82px;
    }
  }

  @media (min-width: $desktop-width) {
    .form-page & {
      margin-bottom: 56px;
    }

    &--photo {
      margin-bottom: 79px;
    }
  }
}
