.main-nav {
  position: relative;
  z-index: 1;

  min-height: 56px;

  background-color: #ffffff;
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;

  width: 22px;
  height: 22px;

  background-repeat: no-repeat;
  background-size: cover;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  .main-nav--closed & {
    width: 23px;
    background-image: url('../img/icon-menu.svg');
    background-size: 23px auto;
  }

  .main-nav--opened & {
    background-image: url('../img/icon-cross.svg');
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}


.main-nav__list {
  @media (max-width: $mobile-width-only) {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    width: 100%;

    background-color:  #81B3D2;

    .main-nav--closed & {
      display: none;
    }
  }

  @media (min-width: $tablet-width) {
    display: flex;
    width: 768px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
  }
}

.main-nav__item {
  @media (max-width: $mobile-width-only) {
    border-bottom: 1px solid rgba(1, 1, 1, 0.2);
    border-top: 1px solid rgba(255, 255, 255, 0.2);

    &:last-child {
      border-bottom: none;
    }

    &:first-child {
      border-top: none;
    }
  }

  @media (min-width: $tablet-width) {
    &:nth-child(4n-1) {
      margin-left: 156px;
    }
  }

  @media (min-width: $desktop-width) {
    &:nth-child(4n-1) {
      margin-left: 244px;
    }
  }
}

.main-nav__link {
  display: block;

  padding: 14px 20px;

  font-size: 18px;
  line-height: 27px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;

  @media (max-width: $mobile-width-only) {
    &:active {
      color: rgba(255, 255, 255, 0.3);
    }

    &:hover {
      background-color: #669EC0;
    }

    &:active {
      background-color: #5496BD;
    }
  }

  @media (min-width: $tablet-width) {
    width: 153px;
    padding: 16px;
    padding-bottom: 14px;

    font-size: 14px;
    line-height: 26px;
    color: #000000;

    &:hover {
      color: #81B3D2;
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }

    &--current {
      color: #766357;

      &:hover {
        color: #604E43;
      }

      &:active {
        color: rgba(#604E43, 0.3);
      }
    }
  }

  @media (min-width: $desktop-width) {
    width: 239px;
  }


}

.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;

  transform: translateX(-50%);
}

.main-nav__logo-img {
  @media (min-width: $desktop-width) {
    width: 187px;
  }
}
