.photo {
  margin-bottom: 33px;

  @media (min-width: $tablet-width) {
    width: 768px;
    margin: 0 auto;
    margin-bottom: 58px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;

    margin-bottom: 57px;
  }
}

.photo__list {

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    padding: 0 60px;
  }
  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.photo__item {
  width: 320px;
  padding: 0 20px;
  padding-top: 22px;
  margin: 0 auto;
  margin-bottom: 14px;

  border-top: 1px solid #E5E5E5;

  &:last-child {
    padding-bottom: 14px;
    margin-bottom: 0;
    border-bottom: 1px solid #E5E5E5;
  }

  @media (min-width: $tablet-width) {
    width: 50%;
    padding: 0;
    margin: 0;
    border: none;

    &:first-child {
      width: 100%;
    }

    &:last-child {
      padding-bottom: 0;
      border: none;
    }
  }

  @media (min-width: $desktop-width) {
    position: relative;
    width: 33.333333%;
  }
}

.photo__img {
  display: block;

  width: 280px;
  height: 280px;
  margin: 0 auto;
  margin-bottom: 10px;

  object-fit: contain;
  object-position: top center;

  @media (min-width: $tablet-width) {
    width: 324px;
    height: 267px;
    margin-bottom: 0;

    &--big {
      width: 648px;
      height: 400px;
    }
  }

  @media (min-width: $tablet-width) {
    width: 400px;

    &--big {
      width: 1200px;
      height: 400px;
    }
  }
}

.photo__info {
  @media (min-width: $tablet-width) {
    position: relative;
    padding: 15px 20px;
    padding-bottom: 16px;
    background-color: #F2F2F2;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(#000000, 0.3);
  }
}

.photo__name {
  display: block;
  margin-bottom: 1px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}

.photo__author {
  display: block;
  margin-bottom: 11px;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}

.photo__likes {
  display: block;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 50%;
    right: 0;

    width: 84px;

    transform: translateY(-50%);
  }
}

.photo__icon {
  margin-right: 14px;
  vertical-align: baseline;
  opacity: 0.3;
  fill: #000000;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    margin-right: 10px;
  }

  @media (min-width: $desktop-width) {
    opacity: 1;
    fill: #ffffff;

    &:hover {
      opacity: 0.6;
    }
  }
}

.photo__likes-number {
  line-height: 24px;
  font-weight: bold;
  color: #000000;

  @media (min-width: $desktop-width) {
    color: #ffffff;
  }
}
