.find-hotel {
  width: 320px;
  padding-top: 57px;
  padding-bottom: 49px;
  margin: 0 auto;

  text-align: center;

  @media (min-width: $tablet-width) {
    width: auto;
    padding-bottom: 58px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 65px;
  }
}

.find-hotel__text {
  padding: 0 29px;
  margin-bottom: 28px;
  line-height: 23px;
  text-transform: uppercase;

  & br {
    display: none;

    @media (min-width: $tablet-width) {
      display: inline;
    }
  }

  @media (min-width: $tablet-width) {
    margin: 0 auto;
    margin-bottom: 37px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 42px;
  }
}

.find-hotel__btn {
  width: 280px;

  @media (min-width: $tablet-width) {
    width: 458px;
  }
}
