@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;

  font-display: swap;
  src: local("PT Sans"),
 local("PTSans"),
    url("../fonts/ptsans.woff2")
    format("woff2"),
    url("../fonts/ptsans.woff") format("woff");
}

@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;

  font-display: swap;
  src: local("PT Sans Bold"),
 local("PTSans-Bold"),
    url("../fonts/ptsansbold.woff2") format("woff2"),
    url("../fonts/ptsansbold.woff") format("woff");
}
